// import Link from "@/components/Link";
import Navigate from "@/components/Navigate";
const NotFound = lazy(() =>
  import(/* webpackChunkName: "not_found" */ "@/views/NotFound")
);
const Depth = lazy(() => import(/*webpackChunkName: "depth"*/ "@/views/Depth"));
const Themes = lazy(() =>
  import(/*webpackChunkName: "themes"*/ "@/views/Themes")
);
const Exclusive = lazy(() =>
  import(/*webpackChunkName: "exclusive"*/ "@/views/Exclusive")
);
export default function App() {
  const navList = [
    { to: "/Telegraphs", title: "" },
    { to: "/Exclusive", title: "" },
    { to: "/Themes", title: "" },
    { to: "/Depth", title: "" },
  ];
  return (
    <>
      <Routes>
        <Route path="/" element={<Navigate to="/Telegraphs" />} />
        <Route path="/Telegraphs" element={<Telegraphs />} />
        <Route
          path="/Exclusive"
          element={
            <Suspense fallback={<Icon icon="line-md:loading-twotone-loop" />}>
              <Exclusive />
            </Suspense>
          }
        />
        <Route
          path="/Themes"
          element={
            <Suspense fallback={<Icon icon="line-md:loading-twotone-loop" />}>
              <Themes />
            </Suspense>
          }
        />
        <Route
          path="/Depth"
          element={
            <Suspense fallback={<Icon icon="line-md:loading-twotone-loop" />}>
              <Depth />
            </Suspense>
          }
        />
        <Route
          path="*"
          element={
            <Suspense fallback={<Icon icon="line-md:loading-twotone-loop" />}>
              <NotFound />
            </Suspense>
          }
        />
      </Routes>
      {navList.map(({ to, title }) => (
        <NavLink
          key={to}
          className={({ isActive }) =>
            isActive ? "text-red-600" : "text-black"
          }
          to={to}
        >
          {title}
        </NavLink>
      ))}
      {/* {navList.map(({ to, title }) => (
        <NavLink
          key={to}
          style={({ isActive }) => {
            return {
              color: isActive ? "red" : "black",
            };
          }}
          to={to}
        >
          {title}
        </NavLink>
      ))} */}
    </>
  );
}
